<template>
  <div class="main-layout">
    <header class="main-header">
      <div class="header-container">
        <div class="logo-container">
          <router-link to="/">
            <img src="@/assets/logo.svg" alt="AI2Cloth Logo" class="logo-img">
            <span class="logo-text">AI2Cloth</span>
          </router-link>
        </div>
        <nav class="main-nav">
          <router-link to="/">È¶ñÈ°µ</router-link>
          <router-link to="/dashboard">ÂºÄÂßãÂàõ‰Ωú</router-link>
          <router-link to="/payment">ÈíªÁü≥ÂïÜÂ∫ó</router-link>
        </nav>
        <div class="user-actions">
          <template v-if="isAuthenticated">
            <div class="user-dropdown" @click="toggleDropdown" :class="{ active: dropdownOpen }">
              <span class="dropdown-trigger">
                <span class="user-icon">üë§</span> {{ currentUser.username }} <span class="arrow-icon">‚ñº</span>
              </span>
              <div class="dropdown-menu" v-show="dropdownOpen">
                <div class="dropdown-item" @click="handleCommand('dashboard')">ÊàëÁöÑ‰∏ªÈ°µ</div>
                <div class="dropdown-divider"></div>
                <div class="dropdown-item" @click="handleCommand('logout')">ÈÄÄÂá∫ÁôªÂΩï</div>
              </div>
            </div>
            <div class="diamonds-info">
              <span class="diamond-icon">üíé</span> {{ currentUser.diamonds }}
            </div>
          </template>
          <template v-else>
            <router-link to="/login" class="auth-link">ÁôªÂΩï</router-link>
            <router-link to="/register">
              <button class="register-button">Á´ãÂç≥Ê≥®ÂÜå</button>
            </router-link>
          </template>
        </div>
      </div>
    </header>
    <main class="main-content">
      <router-view :key="$route.fullPath" />
    </main>
  </div>
</template>

<script>
/**
 * @description ‰∏ªÈ°µÈù¢Â∏ÉÂ±ÄÔºåÂåÖÂê´Â§¥ÈÉ®ÂØºËà™ÂíåÂÜÖÂÆπÂå∫Âüü
 */
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'MainLayout',
  data() {
    return {
      dropdownOpen: false
    };
  },
  computed: {
    ...mapGetters('auth', ['isAuthenticated', 'currentUser'])
  },
  mounted() {
    // ÁÇπÂáªÂ§ñÈÉ®ÂÖ≥Èó≠‰∏ãÊãâËèúÂçï
    document.addEventListener('click', this.handleClickOutside);
  },
  beforeDestroy() {
    document.removeEventListener('click', this.handleClickOutside);
  },
  methods: {
    ...mapActions('auth', ['logout']),
    toggleDropdown() {
      this.dropdownOpen = !this.dropdownOpen;
    },
    handleClickOutside(event) {
      if (!this.$el.querySelector('.user-dropdown').contains(event.target)) {
        this.dropdownOpen = false;
      }
    },
    handleCommand(command) {
      this.dropdownOpen = false;
      if (command === 'logout') {
        this.logout();
      } else if (command === 'dashboard') {
        this.$router.push('/dashboard').catch(() => {});
      }
    }
  }
};
</script>

